.section {
    background-color: #fff;
    margin-bottom: 10px;
    box-shadow: 0px 0px 5px 1px #e4dfdf;
    font-size: 17px;
}

.section__title {
    font-size: 23px;
    margin: 0;
}

.section__title--form {
    border-bottom: 2px solid #e4dfdf;
    padding: 20px;
}

.section__body {
    padding: 30px 20px;
}

.section__header {
    display: grid;
    grid-template-columns: 1fr auto;
    border-bottom: 2px solid #e4dfdf;
    padding: 20px;
}

@media (max-width: 767px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}